*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
.chat-page{
    background-color: rgb(52, 95, 181);
    height: 100vh;
}
.navbar{
    background-color: rgb(58, 186, 94);
    padding: 10px 30px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.chat-heading{
    align-self: center;
    color: rgb(51, 14, 86);
    font-size: 30px;
    font-weight: 700;
}
.chat-container{
    padding: 20px;
    display: flex;
    flex-direction: column;
    background-color: rgb(52, 95, 181);
    height: 65vh;
    width: 100%;
    background-size: cover;
    overflow-y: auto;
}
.message-container{
    overflow-y:scroll;
    overflow-x:hidden;
}
.message-container::-webkit-scrollbar{
    display: none;
}

#right.messageContainer{
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
#left.messageContainer{
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}

 .message{
    padding:10px 15px;
    background-color: aqua;
    color: rgb(47, 11, 47);
    font-weight: 600;
    border-radius: 10px;
    width: 100%;
}


.send-container{
    margin: 0px;
    padding: 10px;
}
.sendForm{
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}
.message-input{
    width: 80%;
    background-color: rgb(94, 104, 190);
    border-radius: 10px 0px 0px 10px;
    outline: none;
    border: solid 0px red;
    padding: 10px;
}
.message-input:focus{
    width: 80%;
    background-color: rgb(94, 104, 190);
    border-radius: 10px 0px 0px 10px;
    outline: none;
    border: solid 2px rgb(42, 36, 132);
    padding: 10px;
}
.send-button{
    background-color: rgb(207, 226, 239);
    width: 7%;
    border-radius: 0px 10px 10px 0px;
    outline: none;
    border: solid 0px red;
    cursor: pointer;
}
.send-button:hover{
    background-color: rgb(207, 226, 239);
    width: 7%;
    border-radius: 0px 10px 10px 0px;
    outline: none;
    border: solid 2px rgb(42, 36, 132);
}